<template>
  <div id="blood">
    <el-container>
      <el-header class="header">上海药械化greenplum数据库数据流向查询系统</el-header>
      <el-container>
        <el-header>
          <el-form :inline="true" :model="param" class="demo-form-inline">
            <el-form-item label="模式名">
              <el-input v-model="param.schema" placeholder="place input scheamname"></el-input>
            </el-form-item>
            <el-form-item label="表名">
              <el-input v-model="param.tablename" placeholder="place input tablemname"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getBloodRelatrionship(param.schema,param.tablename)">查询</el-button>
            </el-form-item>
          </el-form>
        </el-header>
        <el-main>
          <el-tree v-show="isShow" background-color="#545b64" text-color="#fff" 
            active-text-color="#ffd04b"  class="filter-tree"
            :data="ship" :props="defaultProps">
            </el-tree>
        </el-main>
      </el-container>
  </el-container>
    
  </div>
</template>

<script>
export default {
  name: 'Blood',
   data(){
    return{
      ship:[],
      param:{
        tablename:"v_hzp_ywgl_jgxdr_dxfb",
        schema:"sync_bi"
      },
      
      isShow:true,
      data2:{},
      defaultProps: {
	      children: 'children',
	      label: 'title'
	    }
    }
   },
   methods:{
     getBloodRelatrionship:function(schema,tablename){
        this.$nextTick(function() {
          this.$axios.get(this.HOST + "/api/bloodRatelationShip?schema="+ schema + "&tablename="+ tablename).then(res =>{
            this.ship = [res.data];
            this.isShow = true
            debugger
          });
        })
     }
   },
   mounted(){
    this.$nextTick(function() {
      this.$axios.get(this.HOST + "/api/bloodRatelationShip?schema="+ this.param.schema + "&tablename="+ this.param.tablename).then(res =>{
        this.ship = [res.data];
        this.isShow = true
        debugger
      });
    })
  }
}
</script>

<style>
.header{
    background: #000;
    height: 50px!important;
    color: #909399;
    line-height: 50px;
    font-size: 22px;
    padding-left: 10px;
}
</style>


